import { useState, useRef } from 'preact/hooks';
import { render, fireEvent } from '@testing-library/preact';

import { expect } from 'chai';
import { spy } from 'sinon';

import { Button } from '../../UNSAFE_Button';

import { Menu } from '../Menu';
import { SelectMenuItem } from '../SelectMenuItem';
import { SelectMultipleMenuGroup } from '../SelectMultipleMenuGroup';
import { RootEnvironmentProvider } from '../../UNSAFE_Environment';
import { MenuValueUpdateDetail } from '../menuUtils';
import { getIcon, setObserverConfig, getAllMenuItemsByRole } from './menuTestsUtils';

const SelectMenu = ({
  initialValue,
  onCommit
}: {
  initialValue?: string[];
  onCommit?: (detail: MenuValueUpdateDetail<string[]>) => void;
}) => {
  const [selectMultipleValue, setSelectMultipleValue] = useState(initialValue);

  const handleCommit = ({ value }: MenuValueUpdateDetail<string[]>) => {
    setSelectMultipleValue(value);
  };

  const ref = useRef(null);

  return (
    <>
      <RootEnvironmentProvider>
        <Button ref={ref} label="Text Wrapping" />
        <Menu isOpen={true} anchorRef={ref}>
          <SelectMultipleMenuGroup value={selectMultipleValue} onCommit={onCommit || handleCommit}>
            <SelectMenuItem value="bold" label="Bold"></SelectMenuItem>
            <SelectMenuItem value="italics" label="Italics"></SelectMenuItem>
            <SelectMenuItem value="underline" label="Undeline"></SelectMenuItem>
          </SelectMultipleMenuGroup>
        </Menu>
      </RootEnvironmentProvider>
    </>
  );
};

describe('Test Menu Component', () => {
  beforeEach(() => {
    setObserverConfig();
  });

  it('onCommit called when selecting item', async function () {
    const dummyFunc = spy();
    const content = render(<SelectMenu onCommit={dummyFunc} />);
    const menuItems = getAllMenuItemsByRole(content, 'menuitemcheckbox');
    fireEvent.click(menuItems[0]);

    expect(dummyFunc.calledOnce).to.be.true;
  });

  it('Initial SelectMultipleMenuGroup render without value', async function () {
    const content = render(<SelectMenu />);
    const menuItems = getAllMenuItemsByRole(content, 'menuitemcheckbox');

    const firstItem = menuItems[0];
    const secondItem = menuItems[1];
    const thirdItem = menuItems[2];

    expect(firstItem).not.null;
    expect(firstItem.getAttribute('aria-checked')).equals('false');
    expect(getIcon(firstItem, 'svg')).null;

    expect(secondItem).not.null;
    expect(secondItem.getAttribute('aria-checked')).equals('false');
    expect(getIcon(secondItem, 'svg')).null;

    expect(thirdItem).not.null;
    expect(thirdItem.getAttribute('aria-checked')).equals('false');
    expect(getIcon(thirdItem, 'svg')).null;
  });

  it('Initial SelectMultipleMenuGroup render with value', async function () {
    const content = render(<SelectMenu initialValue={['bold']} />);
    const menuItems = getAllMenuItemsByRole(content, 'menuitemcheckbox');

    const firstItem = menuItems[0];
    const secondItem = menuItems[1];
    const thirdItem = menuItems[2];

    expect(firstItem).not.null;
    expect(firstItem.getAttribute('aria-checked')).equals('true');
    expect(getIcon(firstItem, 'svg')).not.null;

    expect(secondItem).not.null;
    expect(secondItem.getAttribute('aria-checked')).equals('false');
    expect(getIcon(secondItem, 'svg')).null;

    expect(thirdItem).not.null;
    expect(thirdItem.getAttribute('aria-checked')).equals('false');
    expect(getIcon(thirdItem, 'svg')).null;
  });

  it('SelectMultipleMenuGroup changes if selected item is selected again', async function () {
    const content = render(<SelectMenu initialValue={['bold']} />);

    const menuItems = getAllMenuItemsByRole(content, 'menuitemcheckbox');
    fireEvent.click(menuItems[0]);

    const menuItemsAfterClick = getAllMenuItemsByRole(content, 'menuitemcheckbox');

    const firstItem = menuItemsAfterClick[0];
    const secondItem = menuItemsAfterClick[1];
    const thirdItem = menuItemsAfterClick[2];

    expect(firstItem).not.null;
    expect(firstItem.getAttribute('aria-checked')).equals('false');
    expect(getIcon(firstItem, 'svg')).null;

    expect(secondItem).not.null;
    expect(secondItem.getAttribute('aria-checked')).equals('false');
    expect(getIcon(secondItem, 'svg')).null;

    expect(thirdItem).not.null;
    expect(thirdItem.getAttribute('aria-checked')).equals('false');
    expect(getIcon(thirdItem, 'svg')).null;
  });

  it('SelectMultipleMenuGroup selected item changes when selecting it', async function () {
    const content = render(<SelectMenu initialValue={['bold']} />);

    const menuItems = getAllMenuItemsByRole(content, 'menuitemcheckbox');
    fireEvent.click(menuItems[1]);

    const menuItemsAfterClick = getAllMenuItemsByRole(content, 'menuitemcheckbox');

    const firstItem = menuItemsAfterClick[0];
    const secondItem = menuItemsAfterClick[1];
    const thirdItem = menuItemsAfterClick[2];

    expect(firstItem).not.null;
    expect(firstItem.getAttribute('aria-checked')).equals('true');
    expect(getIcon(firstItem, 'svg')).not.null;

    expect(secondItem).not.null;
    expect(secondItem.getAttribute('aria-checked')).equals('true');
    expect(getIcon(secondItem, 'svg')).not.null;

    expect(thirdItem).not.null;
    expect(thirdItem.getAttribute('aria-checked')).equals('false');
    expect(getIcon(thirdItem, 'svg')).null;
  });
});
